import store from '@/stores'
import { divide, multiply } from 'mathjs'

// 
// Math.PI = 180 
function angleToRadian(angle) {
  return (angle * Math.PI) / 180
}

/**
 * 
 * @param   {Object}  point  
 * @param   {Object}  center 
 * @param   {Number}  rotate 
 * @return  {Object}         
 * https://www.zhihu.com/question/67425734/answer/252724399 
 */
export function calculateRotatedPointCoordinate(point, center, rotate) {
  /**
   * 
   *  a(x, y)
   *  c(x, y)
   *  n(x, y)
   *                  tan ??
   * nx = cos * (ax - cx) - sin * (ay - cy) + cx
   * ny = sin * (ax - cx) + cos * (ay - cy) + cy
   */

  return {
    x:
      (point.x - center.x) * Math.cos(angleToRadian(rotate)) -
      (point.y - center.y) * Math.sin(angleToRadian(rotate)) +
      center.x,
    y:
      (point.x - center.x) * Math.sin(angleToRadian(rotate)) +
      (point.y - center.y) * Math.cos(angleToRadian(rotate)) +
      center.y
  }
}

/**
 * 
 * @param  {Object} style  
 * @param  {Object} center 
 * @param  {String} name   
 * @return {Object}        
 */
export function getRotatedPointCoordinate(style, center, name) {
  let point // point 
  switch (name) {
    case 't':
      point = {
        x: style.left + style.width / 2,
        y: style.top
      }

      break
    case 'b':
      point = {
        x: style.left + style.width / 2,
        y: style.top + style.height
      }

      break
    case 'l':
      point = {
        x: style.left,
        y: style.top + style.height / 2
      }

      break
    case 'r':
      point = {
        x: style.left + style.width,
        y: style.top + style.height / 2
      }

      break
    case 'lt':
      point = {
        x: style.left,
        y: style.top
      }

      break
    case 'rt':
      point = {
        x: style.left + style.width,
        y: style.top
      }

      break
    case 'lb':
      point = {
        x: style.left,
        y: style.top + style.height
      }

      break
    default: // rb
      point = {
        x: style.left + style.width,
        y: style.top + style.height
      }

      break
  }

  return calculateRotatedPointCoordinate(point, center, style.rotate)
}

// 
export function getCenterPoint(p1, p2) {
  return {
    x: p1.x + (p2.x - p1.x) / 2,
    y: p1.y + (p2.y - p1.y) / 2
  }
}

export function sin(rotate) {
  return Math.abs(Math.sin(angleToRadian(rotate)))
}

export function cos(rotate) {
  return Math.abs(Math.cos(angleToRadian(rotate)))
}

export function mod360(deg) {
  return (deg + 360) % 360
}

export function changeStyleWithScale(value) {
  return multiply(
    value,
    divide(parseInt(store.state.canvasStyleData.scale), 100)
  )
}

export function toPercent(val) {
  return val * 100 + '%'
}
